import React from 'react'
import { Container, Image, Row, Col, Button } from 'react-bootstrap'

import Layout from "../components/layout/layout"
import SEO from "../components/seo"

import medal from '../images/medal.png'
import audi from '../images/audi1.png'
import './index.css'


const IndexPage = () => (
<Layout>
    <SEO title="Home" />
    <Container fluid="sm" className="text-center">
        <Row>
            <Col>
                New Challenge for you
            </Col>
        </Row>
        <Row>
            <Col>
                <br/>
                <Button href="https://b4sportonline.pl/stronger_than_covid_19/zapisy_na_challenge4u__stronger_than_covid19">Click here to Register</Button>
            </Col>
        </Row>
        <Row>
            <Col>
                <br/>
                <Image src={ medal } fluid/>
                <br/>
            </Col>
        </Row>
        <Row className="font-weight-bold mt-4">
            <Col>
                What is Challenge 4 U designed for?
            </Col>
        </Row>
        <Row className="justify-content-md-center text-center">
            <Col md={8} >
            We create beautiful medals to support you to train,
            complete challenges, celebrate your achievements and 
            finally to motivate you to stay fit and healthy.
            </Col>
        </Row>
        <Row className="font-weight-bold mt-4">
            <Col>
            How to join your challenge?
            </Col>
        </Row>
        <Row>
            <Col>
            It's very easy - here you go...
            </Col>
        </Row>
        <Row className="justify-content-md-center">
                <Image src={ audi } fluid/>
        </Row>
    </Container>
</Layout>
)

export default IndexPage
